<template>
   <div class="header">
      <div class="head_wrap">
          <div class="p_infos">
                <!-- 头像 -->
                <div>
                     <router-link  to="/userList">
                      <div class="Avator">
                        <img  src="../../../static/img/touxiang.jpg">
                      </div>
                     </router-link>
                </div>
                <!-- 个人信息 -->
                <div>
                         <p>
                            <i class="icon iconfont icon-huiyuan"></i>
                             {{personDate.nickname}}
                         </p>

                        <p>
                           <i class="icon iconfont icon-yue"></i>
                           余额:{{personDate.balance}}元
                            <span>
                             <i class="icon iconfont icon-jifen"></i>
                             积分: {{personDate.integral}}
                           </span>
                        </p>
                </div>
                <!--  二维码 -->

                <div>
                  <slot>
                         
                  </slot>   
                </div>
          </div>
          <div class="hradius">
          </div> 
      </div>
  </div>
 

</template>

<script type="text/ecmascript-6">
import {Toast} from 'mint-ui'


export default {
data(){
  return{
    personDate:{
        "nickname": "aghron",
        "balance": "0.00",
        "integral": 780
      },
     }

  },
  
  mounted(){
   this.GLOBAL.USER_NAME=this.personDate.nickname 

},

}
 
</script>

<style lang="stylus" type="text/stylus" scoped>
 /* 头部 */
.header
  .head_wrap
     height:1.6rem
     position:relative
     background:#fec65e
     overflow:hidden
     .p_infos
         height:1.6rem
         display:flex
         div
           display:flex
           justify-content:center
           flex-direction:column
         div:nth-child(1)
             width:1.28rem
             align-items:flex-end
             padding-right:0.05rem
             box-sizing:border-box
             .Avator
                  width:0.95rem
                  height:0.95rem
                  margin: auto
                  justify-content: flex-end
                 img
                    margin: auto;
                    width:0.95rem
                    height:0.95rem
                    border-radius:50%
                    border-buttom:2px solid #bebbb7
         div:nth-child(2)
            flex:1
            align-items:center
            p
               text-align:left
               width:100%
               font-size:0.22rem
            p:nth-child(1)
               color:#333
               font-size:0.3rem 
               height:0.4rem
               line-height:0.4rem
               .iconfont
                   color:#da5e96
                   font-size:0.35rem
            p:nth-child(2)
               color:#56585A
               text-indent:3px
               height:0.4rem
               line-height:0.4rem
               font-size:0.25rem
               padding-bottom:0.1rem
            p:nth-child(2) .iconfont
                  font-size:0.28rem
                  color:#e42b83
            p:nth-child(2)  span .iconfont
                  font-size:0.28rem
                  color:#e42b83
                  margin-left:5px    
         div:nth-child(3)
            width:1rem
            align-items:flex-start
     .hradius
       height:1.2rem
       width:135%
       background:#fff
       position:absolute
       bottom:-0.96rem
       left:-1.1rem
       border-top-left-radius:50%;
       border-top-right-radius:50%;
      
</style>




